@import url(https://fonts.googleapis.com/css2?family=Kalam&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam&display=swap);
@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{box-sizing:border-box;background-color:#e4f0e3;text-align:center;box-sizing:border-box;min-height:100vh;padding:10px}@media (max-width: 400px){.App{padding:6px}}.App .App-container{min-height:90vh;color:#372f3f}.App footer{font-size:14px;padding-top:8px}

.home{padding-top:20px}.home h2{font-family:'Kalam', cursive}@media (max-width: 599px){.home h2{margin:0;padding:0}}

.form-container{display:flex;justify-content:center;flex-wrap:wrap}.form-container .search-form,.form-container .category-form{display:flex;flex-wrap:nowrap;margin:8px 20px}@media (max-width: 400px){.form-container .search-form,.form-container .category-form{font-size:0.8em}}@media (max-width: 320px){.form-container .search-form input[type="text"],.form-container .category-form input[type="text"]{width:100px}}.form-container .search-form span,.form-container .category-form span{margin-right:10px}@media (max-width: 340px){.form-container .search-form span,.form-container .category-form span{display:block}}.form-container .search-form button,.form-container .category-form button{margin-left:10px;border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:3px 4px;cursor:pointer}.form-container .search-form select,.form-container .search-form input,.form-container .category-form select,.form-container .category-form input{outline-style:none;height:32px}.form-container .search-form select option,.form-container .search-form input option,.form-container .category-form select option,.form-container .category-form input option{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.product-wrapper{margin:20px auto;display:flex;flex-wrap:wrap;justify-content:center}@media (max-width: 599px){.product-wrapper{flex-direction:column;align-items:center;margin:20px auto}}.product-wrapper .product-link{text-decoration:none}.products .link{text-decoration:none}.products .shop-btn{border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:10px 16px;cursor:pointer}.wait-wrapper{display:flex;justify-content:center;align-items:center;margin-top:40px}.wait-wrapper div{width:300px}.wait-wrapper div img{width:100%}

.movie-shop-button{border:none;border-radius:3px;background-color:rgba(148,24,3,0.7);color:rgba(255,255,255,0.7);font-family:"Roboto Condensed",sans-serif}.movie-shop-button:hover{background-color:rgba(148,24,3,0.9);color:#fff}.movie-shop-button:active{background-color:#881a07}

.card{width:180px;margin:10px;font-size:14px;color:#372f3f;background-color:rgba(255,255,255,0.6);border:1px solid rgba(128,128,133,0.3);border-radius:6px;box-shadow:3px 5px 5px rgba(62,62,65,0.8)}.card .card-image{margin:0 auto;width:100%;min-height:290px}@media (max-width: 599px){.card .card-image{min-height:0}}.card .card-image img{width:100%;border-top-left-radius:6px;border-top-right-radius:6px}.card .card-title{padding:4px;line-height:16px;min-height:62px}@media (max-width: 599px){.card .card-title{min-height:0}}.card .card-price{padding:6px 4px 20px}.card:hover{background-color:#fff;border:1px solid rgba(62,62,65,0.2);opacity:0.8;cursor:pointer;box-shadow:10px 10px 10px #3e3e41}

.message{font-family:'Kalam', cursive;padding:20px}.message>div{font-size:1.5em;margin-top:20px;margin-bottom:80px}

.contact{padding-top:20px}.contact h2{margin:20px;font-family:'Kalam', cursive}@media (max-width: 599px){.contact h2{margin:0;padding:0}}.contact .contact-info{display:flex;justify-content:center;align-items:center;margin-top:100px;background-color:rgba(255,255,255,0.6);padding:20px;font-family:'Kalam', cursive;font-size:18px}.contact .contact-info img{width:60px;margin:20px;border-radius:50%}@media (max-width: 400px){.contact .contact-info{flex-direction:column}}

.mynav{background-color:rgba(255,255,255,0.3);font-size:16px}.mynav .link:hover{color:#262e26}.mynav .logo{width:60px;margin-left:10px}.mynav .logo img{width:100%}@media (max-width: 599px){.mynav .logo{display:none}}.mynav .basket{margin:0 10px}.mynav .basket .basket-icon{margin-left:4px;cursor:pointer;text-decoration:none}

.detail .detail-wrapper{width:80%;margin:80px auto 40px;display:flex;justify-content:center;align-items:center;flex-wrap:wrap}@media (max-width: 599px){.detail .detail-wrapper{margin:20px auto}}.detail .detail-wrapper .text-wrapper{margin:0 40px;width:300px;display:flex;flex-direction:column;text-align:left;font-size:0.9em;line-height:1.2em}@media (max-width: 400px){.detail .detail-wrapper .text-wrapper{margin:0;width:220px}}.detail .detail-wrapper .text-wrapper h2{font-family:'Kalam', cursive}@media (max-width: 400px){.detail .detail-wrapper .text-wrapper h2{font-size:25px}}.detail .detail-wrapper .text-wrapper span{display:block;text-align:center;margin:10px 0 30px}.detail .detail-wrapper .text-wrapper button{display:block;text-align:center;margin:20px 0 10px}.detail .detail-wrapper .detail-image{width:300px;margin:0 40px}@media (max-width: 400px){.detail .detail-wrapper .detail-image{margin:0;width:220px}}.detail .detail-wrapper .detail-image img{width:100%;border-radius:5px;box-shadow:15px 15px 15px rgba(62,62,65,0.8)}.detail .link{text-decoration:none}.detail .shop-btn{border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:10px 16px;cursor:pointer}.wait-wrapper{display:flex;justify-content:center;align-items:center}.wait-wrapper div{width:300px}.wait-wrapper div img{width:100%}

.nopage h1{margin:60px;font-family:'Satisfy', cursive}.nopage .go-home-link{text-decoration:none}.nopage .go-home-link .shop-btn{border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:10px 16px;cursor:pointer}.nopage .nopage-image{width:200px;margin:40px auto}.nopage .nopage-image img{width:100%}

.cart{padding-top:20px}.cart h2{font-family:'Kalam', cursive}@media (max-width: 400px){.cart h2{margin:0;padding:0}}.cart .cart-wrapper{max-width:800px;margin:0 auto}.cart .cart-wrapper .cart-item-wrapper{display:flex;flex-direction:column;align-items:center}.cart .cart-wrapper .total{text-align:right;padding:10px;margin:40px 0 0 auto;font-size:20px;text-align:center}.cart .cart-wrapper .total span{display:inline-block;width:200px}@media (min-width: 800px){.cart .cart-wrapper .total{text-align:right}}.cart .link{text-decoration:none}.cart .shop-btn{text-align:center;border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:10px 16px;cursor:pointer}

.cart-item{max-width:900px;display:flex;flex-direction:column;align-items:center;padding:10px;background-color:rgba(255,255,255,0.3);border:1px solid #4c4c50}@media (min-width: 800px){.cart-item{flex-direction:row}}.cart-item .item-box{display:flex;justify-content:space-between}@media (max-width: 360px){.cart-item .item-box{flex-wrap:wrap}}.cart-item .item-box .cart-img-container,.cart-item .item-box .cart-item-title,.cart-item .item-box .cart-item-price,.cart-item .item-box .quantity-container,.cart-item .item-box .counter,.cart-item .item-box .cart-item-quantity,.cart-item .item-box .cart-item-total,.cart-item .item-box .cart-remove{display:flex;justify-content:flex-start;align-items:center}@media (min-width: 800px){.cart-item .item-box .cart-img-container,.cart-item .item-box .cart-item-title,.cart-item .item-box .cart-item-price,.cart-item .item-box .quantity-container,.cart-item .item-box .counter,.cart-item .item-box .cart-item-quantity,.cart-item .item-box .cart-item-total,.cart-item .item-box .cart-remove{justify-content:center}}.cart-item .item-box .cart-img-container{width:50px;margin:6px;height:80px}.cart-item .item-box .cart-img-container img{display:block;width:100%}.cart-item .item-box .cart-item-title{width:150px;margin:6px}.cart-item .item-box .cart-item-price{width:150px;margin:6px}.cart-item .item-box .quantity-container{margin:6px}.cart-item .item-box .quantity-container .counter span{width:30px;height:30px;font-size:20px;padding-bottom:5px;background-color:rgba(255,255,255,0.3);border:1px solid #4c4c50;border-radius:50%;display:flex;justify-content:center;align-items:center}.cart-item .item-box .quantity-container .counter span:hover{background-color:rgba(255,255,255,0.8);cursor:pointer}.cart-item .item-box .quantity-container .cart-item-quantity{width:25px;justify-content:center}.cart-item .item-box .cart-item-total{width:150px;margin:6px;padding:10px 0 0 0}.cart-item .item-box .cart-remove{margin:6px}.cart-item .item-box .cart-remove .shop-btn{border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:10px 16px;cursor:pointer}

.checkout{padding-top:20px}.checkout h2{font-family:'Kalam', cursive}@media (max-width: 400px){.checkout h2{margin:0;padding:0}}.checkout .checkout-wrapper{margin-top:60px;display:flex;flex-wrap:wrap;justify-content:center}.checkout .checkout-wrapper>div{margin:0 20px;width:280px}@media (max-width: 400px){.checkout .checkout-wrapper>div{width:200px}}.checkout .checkout-wrapper .order-summary{background-color:rgba(255,255,255,0.3);border:1px solid rgba(83,116,121,0.3);padding:20px;text-align:left;width:100%;height:100%;font-size:14px}.checkout .checkout-wrapper .order-summary h5{margin-bottom:20px;font-family:'Kalam', cursive}.checkout .checkout-wrapper .order-summary .product{display:flex;justify-content:space-between}.checkout .checkout-wrapper .order-summary .product p{width:60%}.checkout .checkout-wrapper .order-summary .product span{width:20%}.checkout .checkout-wrapper .order-summary .total{margin-top:30px}.checkout .checkout-wrapper .order-form{background-color:rgba(255,255,255,0.3);text-align:left;width:100%;margin:0 auto;padding:20px;border:1px solid rgba(83,116,121,0.3)}.checkout .checkout-wrapper .order-form form div{margin:10px 0}.checkout .checkout-wrapper .order-form form input[type=email]{width:160px}@media (max-width: 400px){.checkout .checkout-wrapper .order-form form input[type=email]{width:100px}}.checkout .checkout-wrapper .order-form form .radio{display:flex;flex-direction:column}.checkout .checkout-wrapper .order-form form .radio span{margin-left:4px}.checkout .shop-btn{margin:0 auto;display:block;max-width:300px;border:1px solid rgba(83,116,121,0.3);border-radius:3px;padding:10px 16px;cursor:pointer}.checkout .link{text-decoration:none}.checkout .full-w{width:100%}

